@import 'fonts'; // Шрифты
@import 'reset'; // Сброс стилей
@import 'setting'; // Общие настройки
@import 'vars'; // Переменные
@import 'mixins'; // Миксины


// Компоненты 
@import 'components/footer';
@import 'components/header';


$dark-text: #3A3B3D;
$secondary-color: #CB88FF;
// Стили

*,
body,
textarea,
button {
    font-family: 'Inter', sans-serif;
}




* {
    transition: 0.3s linear;
}


.dark-theme {
    background: #1E1F21;

    .inverse {
        &-text {
            color: #fff !important;
        }

        &-gray-text {
            color: #696363;
        }

        &-bg {
            background: #252626 !important;
        }
    }
}

.burger-menu {


    width: 20px;
    height: 12px;
    position: relative;
    padding: 0;

    span {
        transition: 0.2s linear;

        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3) {
            display: block;
            height: 2px;
            background: $secondary-color;
            width: 100%;
            position: absolute;
            border-radius: 2px;
        }

        &:nth-child(1) {
            transform: translateY(-6px);
        }

        &:nth-child(3) {
            transform: translateY(6px);
        }
    }

}

.burger-menu.opened {
    span {
        &:nth-child(2) {
            width: 0;
        }

        &:nth-child(1) {
            transform: translate(0) rotate(45deg);
        }

        &:nth-child(3) {
            transform: translate(0) rotate(-45deg);
        }
    }
}





.pink-btn {
    padding: 11px 24px;
    color: #fff;
    background: $secondary-color;
    border-radius: 100px;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    transition: 0.2s linear;
    width: max-content;

    &:hover {
        background: darken($secondary-color, 5%);
        color: #fff;
    }
}

.header {
    .logo {
        color: $dark-text;
        font-weight: 700;
        font-size: 35px;
        line-height: 42px;
        color: #3A3B3D;

        span {
            color: $secondary-color;
        }

        @media(max-width: 768px) {
            font-size: 18px;
        }
    }

    .header-btns {
        a {
            font-weight: 700;
            font-size: 16px;
            line-height: 19px;
        }

        .log-link {
            color: #3A3B3D;
            height: max-content;

            &:hover {
                opacity: 0.8;
            }
        }


    }

}








.main-screen {
    h1 {
        font-weight: 700;
        font-size: 60px;
        line-height: 65px;
        color: #1E1F21;

        @media(max-width: 991px) {
            font-size: 48px;
        }

        @media(max-width: 768px) {
            font-size: 30px;
            line-height: 35px;
        }


    }

    p {
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #A5A5A5;
        max-width: 280px;
    }

    &__img {
        img {
            margin-left: 57px;

            @media(max-width: 991px) {
                margin-left: 0;
                max-width: 500px;
                display: block;
                margin: 0 auto;
            }
        }
    }
}


h2.title {
    font-weight: 700;
    font-size: 60px;
    line-height: 65px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #1E1F21;
    max-width: 860px;
    margin: 0 auto;

    @media(max-width: 991px) {
        font-size: 42px;
        line-height: 1;
    }

    @media(max-width: 768px) {
        font-size: 26px;
        line-height: 31px;
        max-width: 320px;
    }
}

img {
    transition: 0.2s linear;
}

.tiles {
    &__item {

        min-height: 100%;



        &:hover {
            img {
                transform: scale(1.02);
            }
        }

        &-inner {
            background: #F9F9F9;
            height: 100%;
            position: relative;
            overflow: hidden;
        }

        p {
            font-weight: 400;
            font-size: 16px;
            line-height: 23px;
            color: #A5A5A5;
        }
    }

    &__title {
        font-weight: 700;
        font-size: 40px;
        line-height: 45px;
        color: #1E1F21;

        @media(max-width: 768px) {
            font-size: 22px;
            line-height: 27px;
        }
    }

    .pink-btn {
        @media(max-width: 480px) {
            width: 100%;
            text-align: center;
        }
    }

    .left-bottom {
        position: absolute;
        max-width: 500px;
        right: -100px;
        bottom: 0;
        width: 100%;
        object-fit: cover;
    }

    &__img {
        @media(max-width: 1200px) {
            position: static !important;
        }
    }


}


.w-sm-100 {
    @media(max-width: 400px) {
        width: 100%;
    }
}






@import 'media'; // Должен быть в конце проекта